.header {

    background-color: $cor-header;
    padding-bottom: 2rem;

        // .logo__cabecalho {
        //     background-color: chocolate;
        // }

        .logo {
            width: 31.2rem;
            margin: 0 auto;
            display: flex;
            justify-content: center;

            @include mobile {
                width: 18rem;
                margin: 0 auto;
                display: flex;
                justify-content: center;
            }

            @include tablet {
                width: 24rem;
                margin: 0 auto;
                display: flex;
                justify-content: center;
            }

        }

        .icone__mobile {
            display: flex;
            justify-content: center;
            width: 60px;
            margin: 0 auto;
            display: none;

                @include mobile {
                    display: flex;
                    justify-content: center;
                    width: 60px;
                    padding-bottom: 1rem;
                    display: block;
                }
        }

        .nav__bloco {
            display: flex;
            flex-wrap: wrap;
            gap: 2rem;
            justify-content: center;
        }

        .nav__link {
            display: flex;
            align-items: center;
            gap: .2rem;
            box-shadow: 0px 1px 10px 1px #fff;
            padding: 0rem .4rem;
            border-radius: 8px;

                &:hover {
                    background-color: #fff;
                    transition: all .8s; 
                }

                &:hover .nav__item {
                    color: #000;
                }

                &:hover .nav__icone {
                    color: #000;
                }

        }

        .nav__icone {
            color: #fff;
            font-size: 1.3rem;

            @include mobile {
                font-size: 1rem;
            }
        }

        .nav__item {
            color: #fff;
            font-family: $fonte-menu;
            font-size: 2rem;

            @include mobile {
                font-size: 1rem;
            }
        }




}
